import {useEffect} from 'react';

export default (
    gMap: any,
    masks: any[],
    image: any,
    gMaskLayer: any
) => {
    // 
    useEffect(() => {
        if (!gMap || !gMaskLayer) {
            return;
        }
        gMaskLayer.addMasks(masks, true);
    }, [gMap, masks, image, gMaskLayer]);
};
