import _ from 'lodash';
import axios from 'axios';
import notification from '../components/notification';

const STATUS_MESSAGE = {
    400: '400',
    401: '401',
    403: '403',
    404: '404',
    406: '406',
    410: '410',
    422: '422',
    500: '500',
    502: '502',
    503: '503',
    504: '504'
};

axios.defaults.withCredentials = true;
axios.defaults.headers.common.Accept = 'application/json';
axios.defaults.headers.common['Content-Type']
    = 'application/json; charset=UTF-8';
axios.defaults.timeout = 60000 * 20; // zhangzhe 2019-04-16 20

export interface RequestOptions {
    silent?: boolean
    [propName: string]: any;
}

export interface AxiosOptions extends RequestOptions{
    url: string
}

export class Request {

    post(url: string, data?: any, options?: RequestOptions) {
        return this.sendRequest({url, method: 'post', data, ...options});
    }

    get(url: string, params?: any, options?: RequestOptions) {
        return this.sendRequest({url, method: 'get', params, ...options});
    }

    put(url: string, data?: any, options?: RequestOptions) {
        return this.sendRequest({url, method: 'put', data, ...options});
    }

    delete(url: string, data?: any, options?: RequestOptions) {
        return this.sendRequest({url, method: 'delete', data, ...options});
    }

    setHeaders(headers: object) {
        axios.defaults.headers.common = {...axios.defaults.headers.common, ...headers};
    }

    beforeSend(options?: AxiosOptions) {
        return options;
    }

    beforeSuccess(data: any) {
        const {success} = data;
        if (success) { // 
            return data.page || data.result;
        }
        return Promise.reject(this.afterFail(data));
    }

    afterFail(data: any) {
        const {message} = data;
        if (message) {
            if (message.redirect) { // 
                window.location.href = message.redirect;
            }
            return message; // 
        }
        return {};
    }

    sendRequest(options: AxiosOptions) {
        const config
            = typeof this.beforeSend === 'function'
                ? this.beforeSend(options) || options
                : options;

        return axios(config)
            .then(response => {
                const {status, statusText} = response;
                if (status > 400 || status < 200) {
                    const error = new Error(
                        `API ${options.url} status is ${status} (${statusText})`
                    );
                    return Promise.reject(error);
                }
                return response.data;
            })
            .then(data => {
                if (typeof this.beforeSuccess === 'function') {
                    return this.beforeSuccess(data);
                }
                return data;
            })
            .catch(error => {
                const errorMessage = _.get(error, 'message') || _.get(error, 'global');
                const silent = _.get(options, 'silent');
                if (errorMessage && !silent) {
                    const errorStatus = _.get(error, 'response.status');
                    const errorContent = _.get(STATUS_MESSAGE, errorStatus) || errorMessage;
                    notification.error(errorContent);
                }
                return Promise.reject(error);
            });
    }
}

export default new Request();
