import _ from 'lodash';
import {useCallback, useEffect} from 'react';
import AILabel from 'ailabel';

import {imageToCenterPointsForPoint, getLabelLabelDeletePosition} from './util';
import {
    LABEL_LABEL_TEXT_ID_PREFIX,
    GFEATURE_STYLE,
    GTEXT_STYLE,
    LABEL_OFFSET,
    FEATURE_TYPE_MAP
} from './constants';
import {Label, Point} from './interface';

export default (
    gFeatureLayer: any,
    gTextLayer: any,
    image: any,
    show: string,
    toAddLabels: Label[],
    addRelatedMarkersAndTexts:(featureId: any, label: Label) => void,
    addLabelDeleteIcon: (featureId: any, label: Label) => void
) => {
    // feature
    const addLabelFeature = useCallback((featureId: any, label: Label) => {
        if (!gFeatureLayer) {
            return;
        }

        // 
        const {select = false, points = [], point, style, type = 'rect', options} = label;
        const originPoints = type === 'point' ? [point] : points;
        const formatPoints = imageToCenterPointsForPoint(originPoints as Point[], image);
        const featureStyle = new AILabel.Style(style || GFEATURE_STYLE);
        // feature
        const featureType = FEATURE_TYPE_MAP[type];
        const feature = new AILabel.Feature[featureType](featureId, formatPoints, {}, featureStyle, options);

        gFeatureLayer.addFeature(feature);
        // feature:selecttrue
        if (select) {
            feature.active();
        }
    }, [gFeatureLayer, image]);

    // 
    const addLabelLabel = useCallback((featureId: any, label: Label) => {
        if (!gTextLayer) {
            return;
        }
        const {label: labelConfig = {}, points = [], type = 'rect'} = label;
        const {name: labelName = '', style: labelStyle} = labelConfig;
        const labelLabelId = `${LABEL_LABEL_TEXT_ID_PREFIX}${featureId}`;
        const formatPoints = imageToCenterPointsForPoint(points, image);
        const labelLabelPoint = getLabelLabelDeletePosition(type, 'label', formatPoints);
        const labelLabelStyle = new AILabel.Style(labelStyle || GTEXT_STYLE);

        const lableLabel = new AILabel.Text(labelLabelId, {
            pos: {
                x: labelLabelPoint.x,
                y: labelLabelPoint.y
            },
            offset: LABEL_OFFSET[type],
            visible: show !== 'hover',
            maxWidth: 100,
            text: labelName
        }, labelLabelStyle);
        gTextLayer.addText(lableLabel);
    }, [gTextLayer, image, show]);

    const addLabels = useCallback(() => {
        _.each(toAddLabels, addLabel => {
            const featureId = addLabel.key || _.uniqueId();
            // return
            if (gFeatureLayer && gFeatureLayer.getFeatureById(featureId)) {
                return; // 
            }
            // 
            addLabelFeature(featureId, addLabel);
            addLabelLabel(featureId, addLabel);
            addLabelDeleteIcon(featureId, addLabel);
            addRelatedMarkersAndTexts(featureId, addLabel);
        });
    }, [
        gFeatureLayer,
        toAddLabels,
        addLabelFeature,
        addLabelLabel,
        addLabelDeleteIcon,
        addRelatedMarkersAndTexts
    ]);

    // label
    useEffect(() => {
        addLabels();
    }, [toAddLabels]);
};
